<template>
  <el-form
    :model="reginsteForm"
    :rules="fieldRules"
    ref="reginsteForm"
    label-position="left"
    label-width="0px"
    class="demo-ruleForm reginste-container"
  >
    <h3 class="title">就业信息系统管理员注册</h3>
    <el-form-item prop="adminId">
      <span>账号</span>
      <el-input type="text" v-model="reginsteForm.adminId" auto-complete="off" placeholder="学号/教工号"></el-input>
    </el-form-item>
    <el-form-item prop="password">
      <span>密码</span>
      <el-input
        type="password"
        v-model="reginsteForm.password"
        auto-complete="off"
        placeholder="密码"
        show-password
      ></el-input>
    </el-form-item>
    <el-form-item prop="confirmPassword">
      <span>确认密码</span>
      <el-input
        type="password"
        v-model="reginsteForm.confirmPassword"
        auto-complete="off"
        placeholder="再次输入密码"
        show-password
      ></el-input>
    </el-form-item>
    <el-form-item prop="adminName">
      <span>姓名</span>
      <el-input
        type="text"
        v-model="reginsteForm.adminName"
        auto-complete="off"
        placeholder="请输入您的姓名"
      ></el-input>
    </el-form-item>
      <el-button
        type="primary"
        style="width:48%;"
        @click.native.prevent="reginsteSubmit('reginsteForm')"
        :loading="reginsteing"
      >立即注册</el-button>
    </el-form-item>
    <el-form-item>
      <p class="registed">
        已有账号，前往
        <router-link to="/login">登录</router-link>
      </p>
    </el-form-item>
  </el-form>
</template>

<script>
import Cookies from "js-cookie";
import { addAdmin } from "../../../../utils/employment.url";
import { getCookie, setCookie } from "../../../../utils/utils.js";
import { aesEncrypt, aesDecrypt } from "@/utils/apiUtil";
// import { isSame } from "@/utils/utils";
var crypto = require("crypto");
export default {
  name: "reginste",
  data() {
    //判断两个是否一致（密码）
    var isSame = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请再次输入密码"));
      } else if (value !== this.reginsteForm.password) {
        callback(new Error("两次输入密码不一致!"));
      } else {
        callback();
      }
    };
    return {
      reginsteing: false,
      reginsteForm: {
        adminId: "",
        password: "",
        confirmPassword: "",
        adminName: "",
      },
      fieldRules: {
        adminId: [
          { required: true, message: "请输入学号/教工号", trigger: "blur" }
        ],
        password: [{ required: true, message: "请输入密码", trigger: "blur" }],
        confirmPassword: [{ validator: isSame, trigger: "blur" }],
        adminName: [{ required: true, message: "请输入姓名", trigger: "blur" }],
      },
      checked: true
    };
  },
  methods: {
    reginsteSubmit(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          var key = "ILOVEKUANGMINYI";
          var password = aesEncrypt(this.reginsteForm.password, key);
          addAdmin(
            {
              adminId: this.reginsteForm.adminId,
              password: password,
              adminName: this.reginsteForm.adminName,
            },
            "post"
          )
            .then(res => {
              if (res.status == 200) {
                this.$router.push("/adminLogin");
                this.$message({
                  message: res.message,
                  type: "success"
                });
              } else {
                this.$message({
                  message: res.message,
                  type: "error"
                });
              }
            })
            .catch(function(res) {
              alert(res);
            });
        }
      });
    }
  },
  created() {}
};
</script>
<style lang="scss" scoped>
.reginste-container {
  -webkit-border-radius: 5px;
  border-radius: 5px;
  -moz-border-radius: 5px;
  background-clip: padding-box;
  margin: 170px auto;
  width: 350px;
  padding: 35px 35px 15px 35px;
  background: #fff;
  border: 1px solid #eaeaea;
  box-shadow: 0 0 25px #cac6c6;
  text-align: center;
  .title {
    font-size: 18px;
    font-weight: 600;
    margin: 0px auto 40px auto;
    text-align: center;
    color: #505458;
  }
  .el-form-item .el-form-item__content {
    span,
    .el-input {
      display: inline-block;
    }
    span {
      width: 20%;
      text-align: right;
      padding-right: 10px;
    }
    .el-input, .el-switch {
      width: 70%;
    }
  }
  .remember {
    margin: 0px 0px 35px 0px;
  }
  .el-radio {
    margin-left: 10px;
  }
  .registed {
    a {
      color: #ff6f6f;
    }
  }
}
</style>